* {
  box-sizing: border-box;
  margin: 0;
}

body {
  height: 100vh;
}

#container {
  height: 100%;
  width: 100%;
  background-color: #f3f3f3;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: Verdana, Tahoma, sans-serif;
  display: flex;
}

.site-header {
  height: 30px;
  width: 100%;
  background-color: #444;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 0 10px;
  display: flex;
}

.header-link {
  color: #f3f3f3;
  font-family: Menlo, Consolas, Courier New, Courier, monospace;
  font-size: 16px;
  text-decoration: none;
}

.github-logo {
  height: 26px;
  filter: invert();
  padding-top: 4px;
}

.calc {
  height: 600px;
  width: 400px;
  flex-direction: column;
  justify-content: space-between;
  gap: 7.5px;
  display: flex;
}

.calc-row {
  flex: 1;
  justify-content: space-between;
  gap: 7.5px;
  display: flex;
}

.output {
  width: 100%;
  color: #222;
  background-color: #e3e3e3;
  border: 2px solid #bbb;
  border-radius: 25px;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  padding: 0 15px;
  display: flex;
}

.operation {
  font-size: 18px;
}

.result {
  justify-content: flex-end;
  align-items: center;
  font-size: 35px;
  display: flex;
}

.btn {
  cursor: pointer;
  color: #222;
  background-color: #bbb;
  border: none;
  border-radius: 25px;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-family: inherit;
  font-size: 30px;
  display: flex;
}

.btn:hover {
  background-color: #ccc;
}

.btn:active {
  background-color: #aaa;
}

.btn-xl {
  flex: 2.05;
}

.btn:last-child {
  color: #fff;
  background-color: #f0964b;
}

.btn:last-child:hover {
  background-color: #ffa553;
}

.btn:last-child:active {
  background-color: #da8743;
}

@media (max-width: 457px) and (max-aspect-ratio: 2 / 3) {
  #container {
    justify-content: flex-start;
  }

  .calc {
    height: 131.25vw;
    width: 87.5vw;
    gap: 1.64vw;
  }

  .calc-row {
    gap: 1.64vw;
  }

  .output {
    border-radius: 5.47vw;
    padding: 0 3.28vw;
  }

  .operation {
    font-size: 3.94vw;
  }

  .result {
    font-size: 7.66vw;
  }

  .btn {
    border-radius: 5.47vw;
    font-size: 6.56vw;
  }
}

@media (max-height: 690px) and (min-aspect-ratio: 2 / 3) {
  #container {
    justify-content: flex-start;
  }

  .calc {
    height: 85vh;
    width: 56.67vh;
    gap: 1.0625vh;
  }

  .calc-row {
    gap: 1.0625vh;
  }

  .output {
    border-radius: 3.5417vh;
    padding: 0 2.125vh;
  }

  .operation {
    font-size: 2.55vh;
  }

  .result {
    font-size: 4.9583vh;
  }

  .btn {
    border-radius: 3.5417vh;
    font-size: 4.25vh;
  }
}

