
/* Global styles */

* {
    box-sizing: border-box;
    margin: 0;
}

body {
    height: 100vh;
}

#container {
    height: 100%;
    width: 100%;
    background-color: #f3f3f3;
    font-family: Verdana, Tahoma, sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

/* Header styles */

.site-header {
    height: 30px;
    width: 100%;
    background-color: #444;
    margin-bottom: 30px;
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-link {
    font-family: Menlo, Consolas, "Courier New", Courier, monospace;
    color: #f3f3f3;
    font-size: 16px;
    text-decoration: none;
}

.github-logo {
    height: 26px;
    padding-top: 4px;
    filter: invert(100%);
}

/* Calculator styles */

.calc {
    height: 600px;
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 7.5px;
}

.calc-row {
    display: flex;
    justify-content: space-between;
    flex: 1;
    gap: 7.5px;
}

.output {
    width: 100%;
    padding: 0 15px;
    background-color: #e3e3e3;
    border: 2px solid #bbb;
    border-radius: 25px;
    color: #222;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-end;
}

.operation {
    font-size: 18px;
}

.result {
    font-size: 35px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.btn {
    padding: 0;
    background-color: #bbb;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    color: #222;
    font-family: inherit;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
}

.btn:hover {
    background-color: #ccc;
}

.btn:active {
    background-color: #aaa;
}

.btn-xl {
    flex: 2.05;
}

.btn:last-child {
    background-color: rgb(240, 150, 75);
    color: white;
}

.btn:last-child:hover {
    background-color: rgb(255, 165, 83);
}

.btn:last-child:active {
    background-color: rgb(218, 135, 67);
}

@media (max-width: 457px) and (max-aspect-ratio: 2/3) {
    #container {
        justify-content: flex-start;
    }

    .calc {
        height: 131.25vw;
        width: 87.5vw;
        gap: 1.64vw;
    }

    .calc-row {
        gap: 1.64vw;
    }

    .output {
        padding: 0 3.28vw;
        border-radius: 5.47vw;
    }

    .operation {
        font-size: 3.94vw;
    }

    .result {
        font-size: 7.66vw;
    }

    .btn {
        border-radius: 5.47vw;
        font-size: 6.56vw;
    }
}

@media (max-height: 690px) and (min-aspect-ratio: 2/3) {
    #container {
        justify-content: flex-start;
    }

    .calc {
        height: 85vh;
        width: 56.67vh;
        gap: 1.0625vh;
    }

    .calc-row {
        gap: 1.0625vh;
    }

    .output {
        padding: 0 2.125vh;
        border-radius: 3.5417vh;
    }

    .operation {
        font-size: 2.55vh;
    }

    .result {
        font-size: 4.9583vh;
    }

    .btn {
        border-radius: 3.5417vh;
        font-size: 4.25vh;
    }
}